#page-header {
  text-align: center;
  justify-content: center;
  margin: 8px;
  display: flex;
}

#cards-container {
  grid-template-columns: repeat(auto-fill, minmax(81px, 1fr));
  grid-auto-rows: minmax(112px, auto);
  justify-items: center;
  gap: .5rem;
  margin: 16px;
  display: grid;
}

.card {
  width: 81px;
  perspective: 1000px;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #bbb;
  margin: 0;
  padding: 0;
  box-shadow: .3em .3em 1em rgba(0, 0, 0, .3);
}

.card:disabled {
  color: unset;
}

.card-inner {
  width: 100%;
  height: 100%;
  text-align: center;
  transform-style: preserve-3d;
  transition: transform .5s;
  position: relative;
}

.card-front, .card-back {
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  position: absolute;
}

.card-front {
  opacity: .8;
  background-color: #e5e5f7;
  background-image: linear-gradient(135deg, #444cf7 25%, rgba(0, 0, 0, 0) 25%), linear-gradient(225deg, #444cf7 25%, rgba(0, 0, 0, 0) 25%), linear-gradient(45deg, #444cf7 25%, rgba(0, 0, 0, 0) 25%), linear-gradient(315deg, #444cf7 25%, #e5e5f7 25%);
  background-position: 10px 0, 10px 0, 0 0, 0 0;
  background-repeat: repeat;
  background-size: 20px 20px;
}

.card-back {
  justify-content: center;
  align-items: center;
  font: 2em sans-serif;
  display: flex;
  transform: rotateY(180deg);
}

.card.selected .card-inner {
  transform: rotateY(180deg);
}

.card.success {
  border-color: #0f0;
}

.card.failure {
  border-color: #ff5050;
}

@media screen and (min-width: 40em) {
  #page-header {
    margin: 16px;
  }

  #cards-container {
    grid-template-columns: repeat(auto-fill, minmax(121px, 1fr));
    grid-auto-rows: minmax(168px, auto);
  }

  .card {
    width: 121px;
  }

  .card-back {
    font: 3em sans-serif;
  }
}

@media screen and (min-width: 90em) {
  #page-header {
    margin: 32px;
  }

  #cards-container {
    grid-template-columns: repeat(auto-fill, minmax(242px, 1fr));
    grid-auto-rows: minmax(336px, auto);
  }

  .card {
    width: 242px;
  }

  .card-back {
    font: 5em sans-serif;
  }
}

